import { useEffect } from "react";
import "../styles/projectPDF.css";

export default function ProjectPDF(props) {
  useEffect(() => {
    console.log(props.path);
  }, [props.path]);
  return (
    <div className="project-pdf-container">
      <object
        data={props.path}
        type="application/pdf"
        width="100%"
        height="100%"
        aria-label="PDF Viewer"
      ></object>
    </div>
  );
}
